.header {
  background-color: #2e7eaf;
  height: 70px;
  padding: 8px 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  &--fixed {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 99;
  }

  img {
    max-height: 100%;
  }
}
